import * as React from "react"
import { Link } from "gatsby"
import Seo from "../components/seo"
import { Form, Button, InputGroup } from '@themesberg/react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faEye } from "@fortawesome/free-solid-svg-icons";
import Parse from '../vendor/Parse'
import { useLocation } from '@reach/router';
import NumberTable from '../components/NumberTable'
import { Modal } from '@themesberg/react-bootstrap';
import useAuth from "../hooks/userAuth";

const { useState, useEffect } = React;
const IndexPage = ({ type = 'numbers', title = 'SG Mobile Number Finding', renderComponent }) => {

    const location = useLocation();
    const qs = new URLSearchParams(location.search)
    const { isLogin } = useAuth()
    const [items, setItems] = useState([])
    const [query, setQuery] = useState(qs.get('search') || "")
    const [checklist, setChecklist] = useState([])
    const [suggestion, setSuggestion] = useState([])
    const [displayInfo, setDisplayInfo] = useState(null)
    const [isLoading, setIsLoading] = useState(false)
    const [isSuggestionLoading, setSuggestionLoading] = useState(false)
    const [activeNumber, setActiveNumber] = useState(null)
    const searchNumbers = async (query) => {
        setIsLoading(true)
        const res = await Parse.Cloud.run('queryNumbers', {
            query,
            type

        })
        setItems(res.data)
        setIsLoading(false)
    }

    useEffect(() => {
        if (activeNumber) {
            setDisplayInfo(<><h2 className="text-center">{activeNumber?.num}</h2>
                {isLogin?.role === 'user' && <h2 className="text-center">Provider: {activeNumber?.sourceId}</h2>}
                {
                    renderComponent && renderComponent(activeNumber)
                }
            </>)
        }
    }, [activeNumber])

    const notWhatsppFlow = ['admin', 'user'].includes(isLogin?.role)
    const getMySuggestion = async () => {
        setSuggestionLoading(true)
        const res = await Parse.Cloud.run('getMySuggestedNumber', {
            type
        })
        setChecklist(res.checklist)
        setSuggestion(res.suggestion)
        setSuggestionLoading(false)
    }

    React.useEffect(() => {

        searchNumbers(query)
        getMySuggestion()

    }, [])

    const handleSearchButton = async () => {

        await searchNumbers(query)

    }

    const handleItem = async (item) => {
        const res = await Parse.Cloud.run('querySpecificNumber', {
            _id: item._id,
            type
        })

        setActiveNumber(res)



    }

    const handleClose = () => {
        setActiveNumber(null)
    }

    const cols = [{ label: 'Number', key: 'num' }, {
        label: 'Check', onRender: (item) => {
            return <Button onClick={() => handleItem(item)}>
                <FontAwesomeIcon icon={faEye} />
            </Button>
        }
    }]



    return <>
        <h3 className="text-center">{title}</h3>
        <h4 className="text-center"> Last Updated: {items?.[0]?.date} </h4>
        {
            isLogin?.role === 'user' && <div className="text-center">
                <h2 className="text-center">
                    For any help. <a style={{ color: '#25D366', textDecoration: 'none' }} href={`https://api.whatsapp.com/send?phone=6587413159`}>Whatsapp</a> Us.
                </h2>
                <h3>Monitoring List</h3>
                <h4> </h4>
                <div className="text-center">
                    <h4>{checklist.join(', ')}</h4>
                    <NumberTable
                        cols={cols}
                        isLoading={isSuggestionLoading}
                        items={suggestion}
                        onItem={(item) => { handleItem(item) }}
                    />

                </div>
            </div>
        }
        <div className="d-xl-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4 flex-column text-center" >
            <Form className="">
                <Form.Group id="topbarSearch">
                    <InputGroup className="input-group-merge search-bar">
                        <InputGroup.Text><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
                        <Form.Control type="text" name="search" placeholder="Search" value={query} onChange={(e) => setQuery(e.target.value)} />
                        <Button onClick={handleSearchButton} variant="primary" size="sm" className="me-2">
                            Find
                        </Button>
                    </InputGroup>
                </Form.Group>
            </Form>
            <NumberTable cols={cols} isLoading={isLoading} items={items} onItem={item => handleItem && handleItem(item)} />
            {
                !isLogin && <h3>To See More Results, create an account <Link style={{ color: 'blue' }} to='/register/'>here</Link></h3>
            }
        </div>


        <Modal as={Modal.Dialog} centered show={activeNumber} onHide={handleClose}>
            <Modal.Header>
                <Modal.Title className="h6">Information</Modal.Title>
                <Button variant="close" aria-label="Close" onClick={handleClose} />
            </Modal.Header>
            <Modal.Body>
                {displayInfo}
            </Modal.Body>
            <Modal.Footer>
                <Button variant="link" className="text-gray ms-auto" onClick={handleClose}>
                    Close
                </Button>
                <Button variant='#25D366' style={{ backgroundColor: '#25D366', textDecoration: 'none' }} as={Link} to={`https://api.whatsapp.com/send?phone=6587413159&text=Enquiry on ${activeNumber?.num}`}>
                    Whatapps Us
                </Button>

            </Modal.Footer>
        </Modal>
    </>
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
